#search-divider-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 70vh;
    width: 100vw;
    background-color: #FFFAFA;
    background-image: url("../image-bg@2x.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

#searchbar-link {
    max-width: 50%;
}

#searchbar-image {
    max-width: 100%;
    -webkit-box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.2);
            box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.2)
}

@media only screen and (max-width: 900px) {

    #search-divider-container {
        height: 40vh;
    }

    #searchbar-link {
        max-width: 100%;
        margin-right: 50px;
        margin-left: 50px;
    }

}


